import { Component, OnInit } from '@angular/core';
import { AngularFire, AuthProviders, AuthMethods, FirebaseListObservable } from 'angularfire2';

import { PlaylistService } from '../playlist.service';
import { Video } from '../shared/video';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  displayName;
  photoURL;

   title = 'app works with firebase!';
  items: FirebaseListObservable<any>[];
  itemPlaylist: any;
  private playlist: Video[] = [];

  constructor(private af: AngularFire,
    private playlistService: PlaylistService) { }

  ngOnInit() {
    this.af.auth.subscribe(authState => {
      if (!authState) {
        // console.log('NOT LOGGED IN');
        this.displayName = null;
        this.photoURL = null;
        return;
      }

      // console.log('LOGGED IN', authState);
      this.displayName = authState.auth.displayName;
      this.photoURL = authState.auth.photoURL;
      console.log(this.photoURL);
    });
    this.getPlaylist();
    // this.items = this.af.database.list('/playlist');
    // var text = '', item;
    // for (let item of this.items) {
    //   console.log(this.items[item]);
    // }

    // console.log(this.items);


    this.playlistService.fetchData();
    console.log('data fetched');
    this.playlistService.playlistChanged.subscribe(
      (videos: Video[]) => this.playlist = videos
    );

    this.itemPlaylist = this.af.database.list('/playlist/0/description');
    console.log(this.itemPlaylist);

    const queryList = this.af.database.list('/playlist', {
      query: {
        limitToLast: 10,
        orderByKey: true
      }
      });

    console.log(queryList);

    // queryList.on('value', snap => {
    //   preObject.innerText = JSON.stringify(snap.val(), null, 3);
    // });

    const preObject = document.getElementById('object');
    const ulList = document.getElementById('list');

    const dbRefObject = this.af.database.object('playlist').$ref.child('0');
    // const dbRefObject = this.af.database.object('object').$ref.child('object');
    const dbRefList =  dbRefObject.child('hobbies');

    // dbRefObject.on('value', snap => console.log(snap.val()));
    dbRefObject.on('value', snap => {
      preObject.innerText = JSON.stringify(snap.val(), null, 3);
    });

    dbRefObject.on('child_added', snap => {
      const li = document.createElement('li');
      li.innerText = snap.val();
      console.log(snap.key + ': ' + snap.val())
    });

    dbRefList.on('child_added', snap => {
      const li = document.createElement('li');
      li.id = snap.key;
      li.innerText = snap.val();
      ulList.appendChild(li);
      console.log(snap.key + ': ' + snap.val())
    });

    dbRefList.on('child_changed', snap => {
      const liChanged = document.getElementById(snap.key);
      liChanged.innerText = snap.val();
    });

    dbRefList.on('child_removed', snap => {
      const liToRemove = document.getElementById(snap.key);
      liToRemove.remove();
    });

    // const auth = this.af.auth();
    // // auth.signInWithEmailAndPassword(email, pass);

    // const access_token = '692751354235233';

    // var credential = auth.FacebookAuthProvider.credential(access_token);


}

  login() {
    this.af.auth.login({
      provider: AuthProviders.Facebook,
      method: AuthMethods.Popup
    }).then(authState => {
      console.log('AFTER LOGIN', authState);
    });
  }

  logout() {
    this.af.auth.logout();
  }

   getPlaylist() {
    this.playlistService.fetchData();
  }
}



