export class Video {
    id: string;
    username: string;
    title: string;
    path: string;
    description:string;
    favorite: boolean;
  constructor(
    id: string,
    username: string,
    title: string,
    path: string,
    description: string) {
      this.id = id;
      this.username = username;
      this.title = title;
      this.path = path;
      this.description = description;
      this.favorite = false;
    }
}



