import { Injectable, EventEmitter } from '@angular/core';
import { Headers, Http, Response } from '@angular/http';

import 'rxjs/Rx';

import { Video } from './shared/video';

@Injectable()
export class PlaylistService {
  playlistChanged = new EventEmitter<Video[]>();
  private playlist: Video[] = [];

  constructor(private http: Http) { }

  fetchData() {
    console.log('fetchData request');
    // return this.http.get('https://recipebook-6775c.firebaseio.com/recipes.json')
    return this.http.get('https://tut07-http.firebaseio.com/playlist.json')
      .map((response: Response) => response.json())
      .subscribe(
        (data: Video[]) => {
          // console.log(data);
          this.playlist = data;
          this.playlistChanged.emit(this.playlist);
        }
      );
  }
}



